import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';




function AppAbout() {
  return (
    <section id="about" className="block about-block">
      <Container fluid>
        <div className="title-holder ">
          <h2 >dolci</h2>
          <div className="subtitle">הקונדיטור הפרטי שלך</div>
        </div>
        <Row>
          <Col sm={6}>
            <Image src={"/img/big/324942419_901518597869884_1275714989711257874_n.webp"} />
          </Col>
          <Col sm={6}>
            ברוכים הבאים לעולם שלנו, שבו כל עוגה היא חגיגה, וכל ביס הוא מסע של עונג. ברוכים הבאים לקונדיטוריה DOLCI.
            <p>המסע שלנו התחיל במשימה פשוטה אך עמוקה: להפוך את החוויה היומיומית של עוגה למשהו מרהיב באמת.</p>
          </Col>
        </Row>
      </Container>
    </section>
  );
}

export default AppAbout;