import Carousel from "react-bootstrap/Carousel";
import React, { useEffect, useState } from "react";
import { API_URL, doApiGet } from "../services/apiService";
import { TfiShoppingCart } from "react-icons/tfi";
import { Link } from "react-router-dom";

function AppHero() {
  const [ar, setAr] = useState([]);
  useEffect(() => {
    doApi();
  }, []);

  const doApi = async () => {
    const url = API_URL + "/devices";
    try {
      const heroData = await doApiGet(url);
      console.log(heroData);
      setAr(heroData);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <section  className="hero-block mt-0 ">
      {ar.length>0?
      <Carousel >
        {ar.slice(0, 4).map((hero) => {
          return (
            <Carousel.Item  key={hero._id}>
              <div
                style={{
                  backgroundImage: `url(${hero.ImageURL})`,
                  backgroundSize: "cover", // Example additional style
                  backgroundPosition: "center", // Example additional style
                  
                }}
                className="d-block w-100 hero-img"
              >
                {/* Content of the carousel item goes here */}
              </div>
              <Carousel.Caption>
                <h2>{hero.Name}</h2>
                <p >{hero.Description}</p>
                <Link  className="btn btn-primary text-light btn-primart" to={"/PurchasePage/" + hero._id}>
                  קנה עכשיו <TfiShoppingCart  />
                  </Link>
              </Carousel.Caption>
            </Carousel.Item>
          );
        })}
      </Carousel>
      : <h2>Loading....</h2>
    }
    </section>
  );
}

export default AppHero;
